import store from "@/store/index"
import { mapState } from 'vuex'

// store/index.jsvuexstate
let $mcStoreKey = [];
$mcStoreKey = store.state ? Object.keys(store.state) : [];

const mcStore = {
  data(){
    return {
      abc: 'mixin'
    }
  },
  beforeCreate() {
    // vuex$u
    // vuexstateuser.name"" => this.$u.vuex('user.name', '')
    // vuexstateversion1.0.1 => this.$u.vuex('version', '1.0.1')
    this.$mc.vuex = (name, value) => {
      store.commit('$mcStore', {
        name,value
      })
    }
  },
  computed: {
    // vuexstatemixin
    ...mapState($mcStoreKey)
  }
}

export {store ,mcStore}



