import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let lifeData = {}

try {
    // lifeDataAPP
    lifeData = JSON.parse(localStorage.getItem('lifeData')) || {};
    console.log('localStorage.lifeData: ',lifeData)
} catch (e) {
    console.log(e)
}

// APPstate
let saveStateKeys = ['vuex_user','vuex_tabsArr','vuex_active_tabKey'];

// 
const saveLifeData = function (key, value) {
    // 
    if (saveStateKeys.indexOf(key) !== -1) {
        // lifeData
        let tmp = JSON.parse(localStorage.getItem('lifeData'));
        // APPlifeData{}
        tmp = tmp ? tmp : {};
        tmp[key] = value;
        // lifeData
        localStorage.setItem('lifeData', JSON.stringify(tmp));
    }
}

const store = new Vuex.Store({
    state: {
        // lifeDatastate
        // vuex_
        vuex_user: lifeData.vuex_user ? lifeData.vuex_user : {name: ''},
        // vuex_versionlifeData.vuex_version
        vuex_version: '1.0.1',
        vuex_tabsArr: lifeData.vuex_tabsArr ? lifeData.vuex_tabsArr : [],
        vuex_active_tabKey: lifeData.vuex_active_tabKey ? lifeData.vuex_active_tabKey : null,
        vuex_pageReload: true
    },
    mutations: {
        $mcStore(state, payload) {
            // stateuser.info.score = 1
            let nameArr = payload.name.split('.');
            let saveKey = '';
            let len = nameArr.length;
            if (len >= 2) {
                let obj = state[nameArr[0]];
                for (let i = 1; i < len - 1; i++) {
                    obj = obj[nameArr[i]];
                }
                obj[nameArr[len - 1]] = payload.value;
                saveKey = nameArr[0];
            } else {
                // state
                state[payload.name] = payload.value;
                saveKey = payload.name;
            }
            // 
            saveLifeData(saveKey, state[saveKey])
        }
    }
})

export default store




