/**
 * 
 * @param {number} extraHeight ( Number,74) 
 * @param {reactRef} ref Tableref
 */
 export function getTableScroll({ extraHeight, ref }={}) {
    if (typeof extraHeight == "undefined") {
      //  64 + 10
      extraHeight = 120
    }
    let tHeader = null
    if (ref && ref.current) {
      tHeader = ref.current.getElementsByClassName("ant-table-thead")[0]
    } else {
      tHeader = document.getElementsByClassName("ant-table-thead")[0]
    }
    //
    let tHeaderBottom = 0
    if (tHeader) {
      tHeaderBottom = tHeader.getBoundingClientRect().bottom
    }
    // --
    let height = document.body.clientHeight - tHeaderBottom - extraHeight
    // let height = `calc(100vh - ${tHeaderBottom + extraHeight}px)`
    // ,
    if (ref && ref.current) {
      let placeholder = ref.current.getElementsByClassName('ant-table-placeholder')[0]
      if (placeholder) {
        placeholder.style.height = height
        placeholder.style.display = "flex"
        placeholder.style.alignItems = "center"
        placeholder.style.justifyContent = "center"
      }
    }
    return height
  }
  


